* {
  text-align: center;
  font-family: "Poppins", sans-serif;
  color: #d3d4d4;
}

body {
  background-image: url("starry-night.jpg");
  margin: 3em;
}

.characterImg {
  margin: 20px 0 0 0;
  border-radius: 20px;
  width: 11em;
}


button {
  padding: 5px 13px;
  margin: 20px 20px 30px;
  border: none;
  border-radius: 10px;
  font-size: 25px;
  outline: none;
  color: #000;
  background: linear-gradient(to right, #2fa6aa, #227477);
}

button:hover {
  cursor: pointer;
  color: #3a3a3a;
  background: #48bcc0;
}

li {
  list-style: none;
  display: inline;
  padding: 5px 10px;
  cursor: pointer;
  user-select: none;
  transition: background-color .2s;
  border-radius: 3em;
}

li:hover {
  color: #000;
  background: #babdbaaf;
  border-radius: 3em;
}

.currentPage {
  color: #000;
  background: #6ccc4e;
}

h3 {
  color: #2fa6aa;
}

h5, 
.locationName {
  margin-top: 6px;
}

.details {
  display: none;
}

.charContainer, 
.locContainer {
  padding: 15px;
  padding-bottom: 2em;
  margin: 2em;
  margin-left: 26em;
  margin-right: 26em;
  border-radius: 10px;
  background: #3d4b4ba2;
  cursor: default;
}

.charContainer:hover, 
.locContainer:hover {
  box-shadow: 0 0 7px #48bcc0;
  transition: box-shadow .1s;
}
